import React from 'react'
import Carrossel from '../Carrossel'

const features = [
  {
    name: 'Os Nossos Quartos',
    description:
      'Oferecemos uma variedade de quartos para atender às suas necessidades. Seja para uma viagem de negócios ou férias em família, temos o quarto perfeito para você.',
  },
  {
    name: 'Os Nossos Serviços',
    description:
      'Oferecemos uma ampla gama de serviços para garantir que sua estadia seja confortável e memorável. Explore as nossas comodidades e desfrute de uma experiência de hospitalidade de primeira classe.',
  },
  {
    name: 'Os Nossos Trabalhadores',
    description:
      'Temos orgulho da nossa equipe dedicada e profissional, que trabalha incansavelmente para garantir que todos os hóspedes tenham uma experiência memorável e agradável.',
  },
  {
    name: 'Advanced security',
    description:
      'Barato, Rapido e 24/7 ao seu dispor!',
  },
]

export default function Example() {
  return (
    <div className="bg-white py-24 sm:py-32">
      <div className="mx-auto max-w-7xl px-6 lg:px-8">
        <div className="mx-auto max-w-2xl lg:text-center">
          <p className="mt-2 text-3xl font-bold tracking-tight text-gray-900 sm:text-4xl">
            Sua estadia confortável começa aqui. Explore os nossos quartos e serviços exclusivos.
          </p>
          <p className="mt-6 text-lg leading-8 text-gray-600">
            Barato, Rapido e 24/7 ao seu dispor!
          </p>
        </div>

        <div>
        <Carrossel/>
        </div>
        
        <div className="mx-auto mt-16 max-w-2xl sm:mt-20 lg:mt-24 lg:max-w-4xl">
          <dl className="grid max-w-xl grid-cols-1 gap-x-8 gap-y-10 lg:max-w-none lg:grid-cols-2 lg:gap-y-16">
            {features.map((feature) => (
              <div key={feature.name} className="relative pl-16">
                <dt className="text-base font-semibold leading-7 text-gray-900">
                  <div className="absolute left-0 top-0 flex h-10 w-10 items-center justify-center rounded-lg bg-indigo-600">
                  </div>
                  {feature.name}
                </dt>
                <dd className="mt-2 text-base leading-7 text-gray-600">{feature.description}</dd>
              </div>
            ))}
          </dl>
        </div>
      </div>
    </div>
  )
}
